
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

import $ from 'jquery'
const list = () => ['', '', '', '']
const titleDetail = {
'ylf': {
sign: '',
title: ')',
term: '',
des: '20-40 *'
},
'hlf': {
sign: '',
title: '',
term: '',
des: ''
},
'wgf': {
sign: '',
title: '/',
term: '',
des: ''
},
'cjpc': {
sign: '',
title: '/',
term: '',
des: '12' +
' ' +
'35' +
'4100%90%80%70%60%50%40%30%20%10%' +
'2%4%10%' +
'22' +
'' +
'10000V;;'
},
'jsshf': {
sign: '',
title: '',
term: '22',
des: ''
},
'yws': {
sign: '',
title: '',
term: '',
des: ''
},
'jdf': {
sign: '',
title: '',
term: '',
des: ''
},
'lsf': {
sign: '',
title: '',
term: '',
des: '10000'
}
}

export default {
components: {
Tab,
TabItem,
Sticky,
Divider,
XButton,
Swiper,
SwiperItem,
XInput,
Group,
Cell,
Tabbar,
TabbarItem,
AlertPlugin
},
data () {
return {
index01: 0,
list2: list(),
detalTitle: titleDetail,
demo2: '',
demoDisabled: 'A',
index: 0,
allTotalMoney: '0.00',
getBarWidth: function (index) {
return (index + 1) * 22 + 'px'
},

isNumber: function (value) {
let patrn = /^\d{1,10}(\.\d{0,2})?$/
if (!patrn.exec(value)) {
if (value.indexOf('.') !== -1) {
return {valid: true}
} else {
return {valid: false, msg: ''}
}
} else {
return {valid: true}
}
}
}
},
methods: {

gotoTop (currentIndex) {
$('#vux_view_box_body').animate({scrollTop: 0}, 300)
},

allPrice (value, $event) {
let inputs = $('input[name^="LS_"]')
let count = 0
for (let i = 0; i < inputs.length; i++) {
let s = $(inputs[i]).val()
if (s === '') continue
count += parseFloat(s)
}
this.allTotalMoney = count.toFixed(2)
},

switchTabItem (index) {
console.log('on-before-index-change', index)
this.$vux.loading.show({
text: 'loading'
})
setTimeout(() => {
this.$vux.loading.hide()
this.index01 = index
}, 1000)
},
onItemClick (index) {
console.log('on item click:', index)
},
addTab () {
if (this.list2.length < 5) {
this.list2 = list().slice(0, this.list2.length + 1)
}
},
removeTab () {
if (this.list2.length > 1) {
this.list2 = list().slice(0, this.list2.length - 1)
}
},
next () {
if (this.index === this.list2.length - 1) {
this.index = 0
} else {
++this.index
}
},
prev () {
if (this.index === 0) {
this.index = this.list2.length - 1
} else {
--this.index
}
},
showtitle (detailTitle) {
this.$vux.alert.show({
title: `${detailTitle.sign}`,
content: `<div class="title_icon_pos">` +
`<span class="title_icon_span"></span>` +
`<p> ${detailTitle.title} </p></br>` +
`<span class="title_icon_span"></span>` +
`<p> ${detailTitle.term} </p> </br>` +
`<span class="title_icon_span"></span>` +
`<p> ${detailTitle.des} </p></br>` +
`</div>`,
buttonText: '',
onShow () {

},
onHide () {

}
})
}
}
}



